.header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 30;
    @include padding-y(var(--offset-s));
	@include flex-center-y;
	background-color: rgba(16, 16, 29, 0.9);
	color: var(--white);

	&__inner {
		@include flex-center-between;
	}

    &__menu {
        @include media('767', max) {
            position: fixed;
            top: 0;
            left: -100%;
            z-index: 101;
            width: 100%;
            height: 100%;
            padding: var(--gap-xxl) var(--container-padding);
            background-color: var(--dark);
            transition: var(--transition300ms) left;
            overflow-y: auto;
        }

        @include media('768', min) {
            margin-right: calc(var(--offset-s) * (-1));
        }

        &.active {
            @include media('767', max) {
                left: 0;
            }
        }
    }
}
