@charset "UTF-8";
* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}
*:before, *:after {
  box-sizing: inherit;
}

audio,
canvas,
progress,
video {
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

template {
  display: none;
}

html {
  font-family: sans-serif;
  word-break: break-word;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-focus-ring-color: rgba(255, 255, 255, 0);
  cursor: default;
}

body {
  font-size: 100%;
  line-height: 1;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

*:focus,
*:active,
*:hover {
  outline: none;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  white-space: pre-wrap;
  tab-size: 4;
}

q {
  quotes: "«" "»";
}

button,
input {
  line-height: normal;
}

button,
input,
select {
  margin: 0;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: button;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

input[type=search] {
  box-sizing: content-box;
  -webkit-appearance: textfield;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

textarea {
  vertical-align: top;
}

button,
input,
select[multiple],
textarea {
  background-image: none;
}

input,
select,
textarea {
  border-radius: 0;
  box-shadow: none;
}

input,
textarea {
  resize: none;
  user-select: text;
}

button,
textarea,
select,
input[type=text],
input[type=password],
input[type=search],
input[type=submit],
input[type=button],
input[type=reset],
input[type=number],
input[type=range] {
  border-radius: 0;
  appearance: none;
}
button::-ms-clear, button::-ms-reveal,
textarea::-ms-clear,
textarea::-ms-reveal,
select::-ms-clear,
select::-ms-reveal,
input[type=text]::-ms-clear,
input[type=text]::-ms-reveal,
input[type=password]::-ms-clear,
input[type=password]::-ms-reveal,
input[type=search]::-ms-clear,
input[type=search]::-ms-reveal,
input[type=submit]::-ms-clear,
input[type=submit]::-ms-reveal,
input[type=button]::-ms-clear,
input[type=button]::-ms-reveal,
input[type=reset]::-ms-clear,
input[type=reset]::-ms-reveal,
input[type=number]::-ms-clear,
input[type=number]::-ms-reveal,
input[type=range]::-ms-clear,
input[type=range]::-ms-reveal {
  display: none;
}

[placeholder]:focus::placeholder {
  color: transparent;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

iframe {
  border: none;
}

ul,
ol {
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
dl,
dd,
dt {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: none;
}
table img {
  max-width: none;
}

address {
  font-style: normal;
}

small {
  font-size: inherit;
}

.no-js-warning {
  position: fixed;
  z-index: 99999;
  width: 100%;
  padding: 15px 20px;
  color: #f00;
  font-size: 20px;
  background: rgba(255, 0, 0, 0.5);
}

:root {
  --color-white-rgb: 255, 255, 255;
  --dark-gray-rgb: 61, 64, 80;
  --opacity-70: 0.7;
  --opacity-85: 0.85;
  --white: rgb(var(--color-white-rgb));
  --light-gray: rgba(var(--color-white-rgb), var(--opacity-85));
  --black: #000;
  --dark: #2A2B30;
  --dark-2: #272733;
  --accent: #3E9CDC;
  --blue: #42A9ED;
  --dark-blue: #286690;
  --gray: #BBB;
  --gray-2: #C3C3C3;
  --gray-bg: #3D4050;
  --gray-border: #6F6D7B;
  --dark-gray: rgba(var(--dark-gray-rgb), var(--opacity-85));
  --container-width: 73.125rem;
  --container-padding: 0.9375rem;
  --header-height: 6.25rem;
  --section-padding: 6.25rem;
  --section-padding_xl: 7.5rem;
  --section-padding_xxl: 9.375rem;
  --gap: 1.875rem;
  --gap-xl: 3.75rem;
  --gap-xxl: 5rem;
  --offset-m: 1.25rem;
  --offset-s: 0.9375rem;
  --offset: 0.75rem;
  --offset-xs: 0.625rem;
  --offset-xxs: 0.3125rem;
  --icon-size: 1.25rem;
  --base-font-size: 16;
  --main-font: "Lato", sans-serif;
  --color-default: var(--black);
  --transition-timing: cubic-bezier(0.4, 0, 0.2, 1);
  --duration300ms: 0.3s;
  --duration1s: 1s;
  --delay: var(--duration300ms);
  --transition300ms: var(--duration300ms) var(--transition-timing);
  --transition1s: var(--duration1s) var(--transition-timing);
  --brs-field: 3px;
  --brs-btn: 4px;
}
@media (max-width: 767px) {
  :root {
    --section-padding: 3.125rem;
    --section-padding_xl: 3.75rem;
    --section-padding_xxl: 4.6875rem;
  }
}
@media (max-width: 767px) {
  :root {
    --gap: 0.9375rem;
    --gap-xl: 1.875rem;
    --gap-xxl: 2.5rem;
  }
}

html {
  box-sizing: border-box;
  scrollbar-color: var(--accent);
  scrollbar-width: thin;
  scroll-behavior: smooth;
}
html::-webkit-scrollbar {
  width: 0.4375rem;
  height: 0.4375rem;
  background-color: var(--dark-gray);
  border-radius: var(--brs-btn);
}
html::-webkit-scrollbar-thumb {
  background-color: var(--accent);
  border-radius: var(--brs-btn);
}

body {
  min-width: 320px;
  font-style: normal;
  font-family: var(--main-font);
  line-height: 1.5;
  font-weight: 400;
  color: var(--color-default);
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
  overflow-x: hidden;
}
body._lock {
  overflow-y: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

@media (min-width: 1101px) {
  .animated {
    transform: translateY(var(--gap-xl));
    opacity: 0;
    transition: var(--transition1s) transform, var(--transition1s) opacity;
  }
}
@media (min-width: 1101px) {
  .animated.in-view {
    transform: translateY(0);
    opacity: 1;
  }
}

.container {
  width: calc(var(--container-width) + var(--container-padding) * 2);
  max-width: 100%;
  padding-right: var(--container-padding);
  padding-left: var(--container-padding);
  margin: 0 auto;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
@media (max-width: 1100px) {
  .wrapper {
    overflow-x: hidden;
  }
}

.page {
  position: relative;
  flex: 1 0 auto;
}

.btn {
  cursor: pointer;
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: var(--main-font);
  transition: var(--transition300ms) color, var(--transition300ms) background-color, var(--transition1s) transform, var(--transition1s) opacity;
  color: currentColor;
}
@media (any-hover: hover) {
  .btn:hover {
    color: var(--accent);
  }
}
.btn.disabled, .btn[disabled] {
  background-color: var(--gray-border);
  color: var(--white);
  cursor: not-allowed;
}
@media (any-hover: hover) {
  .btn.disabled:hover, .btn[disabled]:hover {
    color: var(--white);
  }
}
.btn svg,
.btn .icon, .btn__icon {
  flex-shrink: 0;
  margin-right: var(--offset-xs);
}
.btn--style_border, .btn--style_border-accent {
  display: flex;
  align-items: center;
  justify-content: center;
  border-style: solid;
  background-color: var(--white);
  border-radius: var(--brs-btn);
  text-transform: uppercase;
  color: var(--dark-2);
}
.btn--style_border .btn__icon, .btn--style_border-accent .btn__icon {
  fill: var(--dark-2);
}
@media (any-hover: hover) {
  .btn--style_border:hover, .btn--style_border-accent:hover {
    color: var(--white);
  }
  .btn--style_border:hover .btn__icon, .btn--style_border-accent:hover .btn__icon {
    fill: var(--white);
  }
}
.btn--style_border {
  border-width: 1px;
  border-color: var(--gray-border);
}
@media (any-hover: hover) {
  .btn--style_border:hover {
    background-color: var(--gray-border);
  }
}
.btn--style_border-accent {
  border-width: 2px;
  border-color: var(--accent);
}
@media (any-hover: hover) {
  .btn--style_border-accent:hover {
    background-color: var(--accent);
  }
}
.btn--primary {
  background-color: var(--accent);
  color: var(--white);
  border: 1px solid var(--white);
  border-radius: var(--brs-btn);
  text-transform: uppercase;
}
@media (any-hover: hover) {
  .btn--primary:hover {
    background-color: var(--dark-blue);
    color: var(--white);
  }
}

.btns {
  display: flex;
  flex-wrap: wrap;
  gap: var(--gap);
}

.title {
  font-family: var(--main-font);
  text-transform: uppercase;
}
.title span {
  color: var(--accent);
}

.subtitle {
  font-size: 1.125rem;
  line-height: 1.2222222222;
  color: var(--gray);
}

h1 {
  font-size: 3rem;
  line-height: 1.2083333333;
}

h2 {
  font-size: 2.25rem;
  line-height: 1.1944444444;
  font-weight: 900;
}

.icon {
  flex-shrink: 0;
  width: var(--icon-size);
  height: var(--icon-size);
  transition: var(--transition300ms) fill;
}

.burger-btn {
  display: none;
  width: 1.875rem;
  height: 1.6875rem;
  flex-direction: column;
  justify-content: space-between;
  background: transparent;
  border: none;
}
.burger-btn span {
  width: 100%;
  height: 0.1875rem;
  background-color: var(--white);
}
@media (max-width: 767px) {
  .burger-btn {
    display: flex;
  }
}

.checkout-form {
  display: grid;
  gap: var(--gap);
}
@media (max-width: 767px) {
  .checkout-form {
    gap: var(--gap-xl);
  }
}
@media (min-width: 768px) {
  .checkout-form {
    grid-template-columns: repeat(6, 1fr);
    grid-template-areas: "select select select email email email" "name name name file file file" "range range range range range range" ". . submit submit . .";
  }
}
@media (min-width: 1101px) {
  .checkout-form {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "select email name" "range range file" ". submit .";
  }
}
@media (min-width: 768px) {
  .checkout-form__select {
    grid-area: select;
  }
}
@media (min-width: 768px) {
  .checkout-form__field--email {
    grid-area: email;
  }
}
@media (min-width: 768px) {
  .checkout-form__field--name {
    grid-area: name;
  }
}
@media (min-width: 768px) {
  .checkout-form__file {
    grid-area: file;
  }
}
.checkout-form__file-upload {
  padding: var(--offset);
  font-size: 0.9375rem;
  line-height: 1.2;
}
@media (min-width: 768px) {
  .checkout-form__range {
    grid-area: range;
  }
}
.checkout-form__btn {
  padding: var(--offset);
  font-size: 0.9375rem;
  line-height: 1.4666666667;
}
@media (min-width: 768px) {
  .checkout-form__btn {
    margin-top: 1.5625rem;
    grid-area: submit;
  }
}

.checkout {
  padding: var(--section-padding_xl) 0 var(--section-padding_xxl);
  background-image: url(../images/bg/checkout-bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--dark-2);
  color: var(--white);
}
.checkout__body {
  display: grid;
  row-gap: var(--gap-xxl);
}

.close-btn {
  position: absolute;
  top: 0;
  right: 0;
  padding: var(--gap);
  border: none;
  background: transparent;
  font-size: 2.8125rem;
  line-height: 58%;
  color: var(--white);
}

.field {
  width: 100%;
  padding: var(--offset) var(--offset-xs);
  background-color: var(--light-gray);
  border-radius: var(--brs-field);
  border: 1px solid var(--white);
  font-size: 1.125rem;
  line-height: 1.2222222222;
  color: var(--dark-2);
  transition: var(--transition300ms) border-color;
}
.field::placeholder {
  color: var(--dark-2);
}
.field:focus {
  border-color: var(--blue);
}

input[type=file] {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  opacity: 0;
}

.range__top {
  margin-bottom: var(--offset-m);
  display: flex;
  align-items: center;
  gap: var(--offset-xs);
  font-size: 1.125rem;
  line-height: 1.2222222222;
}
.range__label {
  flex-grow: 1;
}
.range__value {
  color: var(--light-gray);
  white-space: nowrap;
}

.noUi-target {
  background-color: rgba(var(--color-white-rgb), var(--opacity-70));
  border-color: var(--white);
  box-shadow: none;
}
.noUi-handle {
  background-color: var(--blue);
  border-radius: 50%;
  border: 3px solid var(--dark-blue);
  box-shadow: none;
}
.noUi-handle::before, .noUi-handle::after {
  display: none;
}
.noUi-horizontal {
  height: 0.375rem;
}
.noUi-horizontal .noUi-handle {
  top: -0.6rem;
  width: 1.5rem;
  height: 1.5rem;
}

.footer {
  padding-top: var(--gap);
  padding-bottom: var(--gap);
  background-color: var(--dark-gray);
  color: var(--white);
}
.footer__inner {
  display: grid;
}
@media (max-width: 1100px) {
  .footer__inner {
    gap: var(--gap);
  }
}
@media (min-width: 768px) {
  .footer__inner {
    grid-template-columns: repeat(2, minmax(max-content, 1fr));
    grid-template-areas: "pay contacts" "copy copy";
    align-items: center;
  }
}
@media (min-width: 1101px) {
  .footer__inner {
    grid-template-columns: repeat(3, minmax(max-content, 1fr));
    grid-template-areas: "copy pay contacts";
  }
}
.footer__copyright {
  font-size: 0.8125rem;
  line-height: 1.2307692308;
}
@media (min-width: 768px) {
  .footer__copyright {
    grid-area: copy;
  }
}
@media (min-width: 768px) {
  .footer__payment {
    grid-area: pay;
  }
}
@media (min-width: 1101px) {
  .footer__payment {
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    position: relative;
  }
  .footer__payment::before, .footer__payment::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1px;
    height: 1.125rem;
    background-color: var(--gray);
  }
  .footer__payment::before {
    left: 0;
  }
  .footer__payment::after {
    right: 0;
  }
}
@media (min-width: 768px) {
  .footer__contacts {
    grid-area: contacts;
  }
}
@media (min-width: 1101px) {
  .footer__contacts {
    justify-content: flex-end;
  }
}
.footer__contact {
  display: flex;
  align-items: center;
  font-size: 0.8125rem;
  line-height: 1.2307692308;
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 30;
  padding-top: var(--offset-s);
  padding-bottom: var(--offset-s);
  display: flex;
  align-items: center;
  background-color: rgba(16, 16, 29, 0.9);
  color: var(--white);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .header__menu {
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 101;
    width: 100%;
    height: 100%;
    padding: var(--gap-xxl) var(--container-padding);
    background-color: var(--dark);
    transition: var(--transition300ms) left;
    overflow-y: auto;
  }
}
@media (min-width: 768px) {
  .header__menu {
    margin-right: calc(var(--offset-s) * -1);
  }
}
@media (max-width: 767px) {
  .header__menu.active {
    left: 0;
  }
}

.img-cover, .img-contain {
  width: 100%;
  height: 100%;
}
.img-cover {
  object-fit: cover;
}
.img-contain {
  object-fit: contain;
}

.logo {
  display: block;
}
.logo__img {
  width: 15.4375rem;
  height: 2.1875rem;
  object-fit: contain;
}

.menu__list {
  display: flex;
  flex-wrap: wrap;
  gap: var(--gap);
}
@media (max-width: 767px) {
  .menu__list {
    flex-direction: column;
    flex-wrap: nowrap;
  }
}
.menu__link {
  padding: var(--offset-s);
  font-size: 0.8125rem;
  line-height: 1.2307692308;
  text-transform: uppercase;
}
.menu__close {
  display: none;
}
@media (max-width: 767px) {
  .menu__close {
    display: block;
  }
}

.payment {
  display: flex;
  gap: var(--gap);
}
.payment__item {
  display: flex;
  align-items: center;
}
.payment__item-icon {
  flex-shrink: 0;
  margin-right: var(--offset-xs);
}
.payment__item-text {
  font-size: 0.8125rem;
  line-height: 1.2307692308;
  color: var(--gray);
  white-space: nowrap;
}

.promo {
  padding: calc(var(--header-height) + var(--section-padding)) 0 var(--section-padding);
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .promo {
    padding-top: calc(var(--header-height) + var(--gap));
    background-position-x: 60%;
  }
}
@media (min-width: 1101px) {
  .promo {
    min-height: 100vh;
  }
}
.promo__content {
  display: grid;
  row-gap: var(--gap-xxl);
  max-width: 48.75rem;
}
.promo__title {
  font-size: 3rem;
  line-height: 1.2083333333;
  font-weight: 700;
}
@media (max-width: 767px) {
  .promo__title {
    font-size: 1.75rem;
  }
}
.promo__text {
  font-size: 1.25rem;
  line-height: 1.2;
}
@media (max-width: 767px) {
  .promo__text {
    font-size: 1rem;
  }
}
.promo__list-title {
  margin-bottom: var(--offset-xs);
  font-weight: 700;
}
.promo__list {
  display: grid;
  row-gap: var(--offset-xxs);
}
.promo__list-item {
  position: relative;
  padding-left: 1.5625rem;
}
.promo__list-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 1.5625rem;
  height: 1.375rem;
  background-image: url(../images/icons/defis.svg);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .promo__btns {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.promo__btn {
  padding: var(--offset);
  display: flex;
  justify-content: center;
  font-size: 0.8125rem;
  line-height: 1.2307692308;
}
@media (min-width: 768px) {
  .promo__btn {
    min-width: 11.4375rem;
  }
}

.select {
  position: relative;
  border-radius: var(--brs-field);
}
.select__toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  text-align: left;
  transition: var(--transition300ms) background-color, var(--transition300ms) color, var(--transition300ms) border-width, var(--transition300ms) border-color;
}
.select__toggle-arrow {
  fill: var(--black);
  transition: var(--transition300ms) transform, var(--transition300ms) fill;
}
.select__toggle[aria-expanded=true] {
  background-color: rgb(var(--dark-gray-rgb));
  border-color: rgb(var(--dark-gray-rgb));
  color: var(--white);
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.select__toggle[aria-expanded=true] .select__toggle-arrow {
  fill: var(--white);
  transform: rotate(180deg);
}
.select__toggle[aria-expanded=true] ~ .select__dropdown {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}
.select__dropdown {
  position: absolute;
  left: 0;
  right: 0;
  padding: 0.125rem 0.25rem;
  background-color: var(--dark-2);
  border: 1px solid rgb(var(--dark-gray-rgb));
  transition: var(--transition300ms) all;
  z-index: 25;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.select__content {
  padding: 0 var(--offset) 0 var(--offset-xxs);
  height: 10.875rem;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: rgb(var(--dark-gray-rgb));
}
.select__content::-webkit-scrollbar {
  width: 0.4375rem;
  background: rgb(var(--dark-gray-rgb));
  border-radius: var(--brs-field);
}
.select__content::-webkit-scrollbar-thumb {
  background: var(--blue);
  border-radius: var(--brs-field);
}
.select__input {
  display: none;
}
.select__input:checked + label {
  color: var(--blue);
}
.select__label {
  display: flex;
  align-items: center;
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  font-size: 1.125rem;
  line-height: 1.8888888889;
  color: var(--white);
  transition: var(--transition300ms) color;
}
@media (any-hover: hover) {
  .select__label:hover {
    color: #42A9ED;
  }
}

.title-container {
  display: grid;
  row-gap: var(--gap);
  justify-items: center;
  text-align: center;
}

.work-scheme {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(7.5rem, 1fr));
  gap: var(--offset-m);
  align-items: flex-start;
}
@media (min-width: 768px) {
  .work-scheme {
    position: relative;
    gap: var(--gap);
  }
  .work-scheme::before {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 2.5rem;
    width: 70.9401709402%;
    height: 0.375rem;
    background-image: url(../images/dots-line.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
}
@media (min-width: 1101px) {
  .work-scheme::before {
    top: 3.125rem;
  }
}
.work-scheme__item {
  display: grid;
  row-gap: var(--offset-m);
  justify-items: center;
  text-align: center;
}
.work-scheme__item-img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: var(--accent);
  border: 0.3125rem solid var(--dark-blue);
}
@media (min-width: 1101px) {
  .work-scheme__item-img {
    width: 6.25rem;
    height: 6.25rem;
  }
}
.work-scheme__item-img svg {
  width: 3.4375rem;
  height: 3.4375rem;
}
.work-scheme__item-title {
  font-size: 1rem;
  line-height: 1.1875;
}
@media (max-width: 1101px) {
  .work-scheme__item-title {
    font-size: 0.9375rem;
  }
}